.catHome {
  position: relative;
  width: 7cm;
  height: 7cm;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;

  &::before {
    inset: 0;
    content: "";
    z-index: -1;
    filter: brightness(43%);
    border-radius: 50%;
    position: absolute;
    background-image: url("./dfff.jpg");
    background-position: center;
    background-size: cover;
    transition: transform .3s ease;
  }

  &:hover::before {
    transform: scale(1.03);
  }
}

